html {
  body {
    overflow-x: hidden;
    width: 100%;
    .masthead {
      min-height: 700px;
      position: relative;
      h1,h4,p,a {
        color: #fff;
        position: relative;
        z-index: 4;
      }
      h4 {
        font-weight: 400;
        margin-top: 160px;
      }
      &:after {
        content:'';
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.5);
      }
      @media all and (max-width: 767px) {
        padding-bottom: 8vh !important;
        min-height: 80vh;
      }
    }
    .side-by-side {
      .jarallax {
        width: 100%;
        height: 400px;
      }
      p {
        max-width: 90%;
      }
    }
    .company-stats {
      .stat {
        margin: 20px auto;
      }
    }
    .vertical-carousel {
      h4 {
        font-weight: 300;
        margin-top: 8px;
      }
      .slick-list {
        overflow: visible !important;
      }
      .slider {
        .slick-slide {
          margin-right: 10px !important;
          &:nth-child(even) {
            margin-top: 20px !important;
          }
        }
      }
      @media all and (max-width: 767px) {
        .slick-list {
          overflow: hidden !important;
        }
        .slider {
          .slick-slide {
            margin-right: 10px !important;
            &:nth-child(even) {
              margin-top: 20px !important;
            }
          }
        }
      }
    }
    .product-callout {
      padding: 0px 8vw;
      .right-aligned {
        .background {
          float:right;
        }
        .foreground {
          margin-top: 12.5%;
        }
      }
      &.product-photos {
        .foreground {
          margin-top: 0px;
          top: 0px;
          height: 35vw;
          width: 30vw;
          -webkit-background-size: contain !important;
          background-size: contain !important;
          -moz-background-size: contain !important;
        }
        .background {
          height: 360px;
          width: 520px;
          margin-top: 60px;
          // -webkit-background-size: contain !important;
          // background-size: contain !important;
          // -moz-background-size: contain !important;
        }
      }
      .images {
        position: relative;
        .background {
          height: 460px; 
          width: 340px;
        }
        .foreground {
          height: 27vw;
          width: 40vw;
          position: absolute;
          left: 15%;
          top: 12.5%;
        }
      }
      .content {
        h4 {
          font-family: 'Gotham';
          font-weight: 300;
          font-size: .875rem;
          color: #000;
          text-transform: uppercase;
          margin: 0px 0px 4px;
        }
        h2 {
          font-family: 'Gotham';
          font-size: 1.4rem;
          color: #000000;
          letter-spacing: 1.71px;
          line-height: 1.4;
          text-transform: uppercase;
          margin: 0px;
        }
        p {
          font-family: 'Gotham';
          font-size: .875rem;
          color: #767676;
          letter-spacing: 0;
          line-height: 1.4;
        }
      }
      @media all and (min-width: 1500px) {
        .images {
          .foreground {
            width: 34vw;
            height: 23vw;
          }
        }
      }
      @media all and (max-width: 767px) {
        .images {
          .background {
            height: 240px;
            width: auto;
          }
        }
        .content {
          margin-top: 4vh;
        }
      }
    }
    .our-story {
      
    }
    .become-rs-influencer {
      .content {
        .box {
          padding: 0px 40px;
        }
      }
      .seperator {
        margin-top: 30px;
      }
    }
    .testimonial-slider {
      .quote {
        font-family: 'Didot';
        font-style: italic;
        font-size: 2rem;
        font-weight: 300;
        line-height: 1.4;
        margin-bottom: 20px; 
      }
      h4 {
        font-size: .875rem;
      }
    }
    .stats-wall {
      h2 {
        font-size: 3.5rem;
        margin: 0px;
        font-family: 'Gotham';
        font-weight: 400;
        line-height: 100%;
      }
      h4 {
        margin: 0px;
        font-family: 'Gotham';
        font-weight: 600;
        font-size: .875rem;
        letter-spacing: 1px;
      }
      @media all and (max-width: 767px) {
        .stat {
          margin: 4vh auto;
        }
      }
    }
    .process-steps {
      h2 {
        font-size: 1.6rem;
      }
    }
    .call-to-action-banner {
      position: relative;
      z-index: 0;
      padding: 12vh 0px !important;
      &:after {
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.5);
        z-index: 0;
      }
      .box {
        position: relative;
        z-index: 1;
        p {
          font-family: 'Gotham';
          color: #fff;
        }
      }
    }
    .brand-wall {
      h2 {
        font-family: 'Didot' !important;
        font-style: italic;
        margin-bottom: 60px;
        font-weight: 300;
        text-transform: none;
      }
      .logo-slider {
        .slick-slide {
          margin: 0px 20px !important;
          .logo {
            width: 75% !important;
          }
          img {
            height: auto !important;
          }
        }
      }
    }
    .large-callout {
      .box {
        position: relative;
      }
      .top-img {
        width: 134px;
        height: 170px;
        margin-left: 100px;
        margin-bottom: 40px;
      }
      .content {
        padding-left: 60px;
        margin-bottom: 60px;
      }
      .left-img {
        // position: absolute;
        // left: 0px;
        // top: 40px;
        width: 420px;
        height: 570px;
        margin-top: 60px;
      }
      .bottom-img {
        // position: absolute;
        // right: 0px;
        // bottom: 0px;
        width: 625px;
        height: 400px;
      }
    }
    .values-callout {
      .container {
        position: relative;
        min-height: 80vh;
        .images {
          max-width: 50%;
          position: absolute;
          right: 0px;
          top: 0px;
          margin-top: 20.5%;
          img {
            display: block;
            margin: 8px auto;
          }
          .row {
          }
        }
        p {
          max-width: 55%;
          margin-bottom: 40px;
        }
      }
      @media all and (max-width: 767px) {
        .container {
          .images {
            position: relative;
            max-width: 100%;
          }
          .btn {
            display: block;
            text-align: center;
            line-height: 40px;
          }
          p {
            max-width: 100%;
          }
        }
      }
    }
    .perks-wall {
      margin-top: 60px;
      .perks {
        margin-top: 60px;
        .perk {
          margin: 20px auto 40px;
          h4 {
            font-size: 1rem;
            letter-spacing: 1px;
            margin-bottom: 8px;
          }
          p {
            font-size: .725rem;
          }
        }
      }
      .top-line {
        height: 3px;
        background: #000;
        width: 50%;
      }
      .perk-gallery {
        height: 400px;
        width: 90%;
      }
    }
    .employee-spotlight {
      background: #f2f2f2;
      h2 {
        font-size: 4rem;
        font-family: 'Didot' !important;
        font-weight: 300;
        font-style: italic;
        text-transform: none;
        margin-bottom: 0px;
      }
      h4 {
        margin-top: 0px;
      }
    }
    .instagram-feed {
      h2 {
        font-family: 'Didot' !important;
        text-transform: none;
        font-style: italic;
        font-weight:300;
        margin: 30px auto;
      }
    }
    .press-timeline {
      background: #f2f2f2;
      width: 100%;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        height: 1px;
        background: #000;
        width: 100%;
        top: 50%;
      }
      .circle {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        line-height: 48px;
        text-align: center;
        background: #000;
        position: relative;
        z-index: 8;
        transition: all .2s ease-in-out;
        -webkit-transition: all .2s ease-in-out;
        -moz-transition: all .2s ease-in-out;
        cursor: pointer;
        i {
          // color: #fff;
          // font-size: 1.4rem;
        }
        &:hover {
          background: #fff;
          border: 1px solid #000;
          i {
            color: #000;
          }
        }
      }
      img {
        position: relative;
        z-index: 5;
        cursor: pointer;
      }
      .content {
        padding: 0px 30px;
      }
      .container {
        // height: 315px;
      }
      .box {
        position: relative;
        z-index: 5;
        background: #f2f2f2;
        border-left: 1px solid #000;
        border-right: 1px solid #000;
        padding: 0px 40px;
      }
    }
    .team-member-wall {
      p {
        text-transform: uppercase;
        font-weight: 300;
        margin-bottom: 0px;
        font-size: .725rem;
      }
      h4 {
        margin-top: 0px;
      }
    }
    .team-member-bio {
      &.left {
        .content {
          padding-left: 40px;
        }
      }
      &.right {
        .content {
          padding-right: 40px;
        }
      }
    }
    .team-member-wall {
      .team-member {
        margin-bottom: 30px;
      }
    }
    .pull-quotes {
      .col-md-4 {
        margin-bottom: 48px;
      }
      article {
        img {
          transition: all .2s ease-in-out;
          -webkit-transition: all .2s ease-in-out;
          -moz-transition: all .2s ease-in-out;
        }
        &:hover {
          img {
            opacity: .8;
          }
        }
        p {
          font-family: 'Didot' !important;
          font-style: italic;
          max-width: 90%;
          margin: 16px auto;
          color: #000;
        }
      }
    }
    .job-postings {
      padding-top: 0px !important;
      #BambooHR {
        padding: 0 40px;
        .BambooHR-ATS-board li {
          font-family: 'Gotham';
        }
      }
    }
    .join-the-family {
      h2 {
        font-family: 'Didot' !important;
        text-transform: none;
        font-style: italic;
        letter-spacing: 0px;
        font-weight: 300;
      }
    }
    .mission-statement {
      .subheading {
        font-size: 1.6rem;
        font-family: 'Didot' !important;
        text-transform: none;
        font-style: italic;
        letter-spacing: 0px;
        font-weight: 300;
      }
    }
    .articles-by-category {
      article {
        margin-bottom: 20px;
        a {
          text-decoration: none;
          h4 {
            margin-bottom: 8px;
            font-size: .725rem;
            letter-spacing: 1px;
          }
          h2 {
            transition: all .2s ease-in-out;
            -webkit-transition: all .2s ease-in-out;
            -moz-transition: all .2s ease-in-out;
            margin-top: 0px;
          }
          &:hover {
            h2 {
              opacity: .9;
            }
          }
        }
      }
    }
  }
}